.footer {
  width: inherit;
  color: rgb(241, 250, 238);
  background-color: rgb(69, 123, 157);
}
.footerContent {
  padding: 20px 0;
}
.linkContainer {
  position: relative;
}
.linkContainer > a > img {
  height: 40px;
}
.linkContainer > a:hover {
  transform: scale(1.15);
  filter: invert(100%);
}
.footerText {
  font-weight: 500;
}
